.Palette {
  height: 100vh;
  display: flex;
  flex-direction: column;
}
.Palette-colors {
  height: 90%;
  font-size: 0;
}

.Palette-footer {
  background-color: #fff;
  height: 5vh;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  font-weight: 800;
}

.Palette-emoji {
  font-size: 1.5rem;
  margin: 0 1rem;
}
